import Vue from 'vue';
import VueComponents from '@nn/vue-components';
import Zoeker from './zoeker';
import { createVueChildContainer } from '../app-vue-shared/createVueChildContainer';
import './_styles.scss';
import { vaAnalytics } from './analytics.js';

Vue.use(VueComponents);

export default class VaZoeker {
  constructor (element, options) {
    this.element = element;
    this.options = options;
    this.app = undefined;
    vaAnalytics('vaZoeker');
  }

  init () {
    this.app = new Vue({
      el: createVueChildContainer(this.element),
      render: h => h(Zoeker, {
        props: {
          apiKey: this.options.apiKey,
        },
      }),
    });
    return this.app;
  }
}
