import { calcDistance } from './geocoding.js';

export const queryAdvisors = (data, categoryMap, typeOfAdvice, searchTerm, maxDistance, currentPosition) => {
  const convertLatLong = (position) => {
    return position / Math.PI * 180;
  };

  return data.map(adviseur => ({
    ...adviseur,
    distance: calcDistance(convertLatLong(adviseur.latitude), convertLatLong(adviseur.longitude), currentPosition.latitude, currentPosition.longitude),
  })).filter(adviseur => isAdvisorVisible(adviseur, categoryMap, typeOfAdvice, searchTerm, maxDistance));
};

export const findAanstelling = (categoryMap, typeOfAdvice) => {
  return categoryMap.find(c => c.name.toLowerCase() === (typeOfAdvice || '').toLowerCase());
};

export const addUniqueId = (array) => {
  let id = 0;
  array.forEach((item) => {
    item.vueId = id;
    id++;
  })
  return array;
}

export const sortAdvisors = (sortBy, advisors) => {
  if (sortBy === 'score') {
    return advisors.sort((a, b) => b.score - a.score || b.reviews - a.reviews || a.distance - b.distance);
  }
  return advisors.sort((a, b) => a.distance - b.distance);

}

const isAdvisorVisible = (adviseur, categoryMap, typeOfAdvice, searchTerm, maxDistance) => {
  return checkKeywords(adviseur, searchTerm) &&
    checkCategory(adviseur, categoryMap, typeOfAdvice) &&
    checkRange(adviseur, maxDistance);
};

const checkKeywords = (adviseur, searchTerm) => {
  const keywords = searchTerm.toLowerCase().split(/\s/g).filter(v => v);
  return keywords.reduce((matched, keyword) => {
    return matched || adviseur.naam.toLowerCase().includes(keyword);
  }, keywords.length === 0);
};

const checkCategory = (adviseur, categoryMap, typeOfAdvice) => {
  const aanstelling = findAanstelling(categoryMap, typeOfAdvice);
  if (aanstelling) {
    return adviseur.aanstellingen &&
      adviseur.aanstellingen.includes(aanstelling.key);
  }
};

const checkRange = (adviseur, maxDistance) => {
  return !maxDistance || adviseur.distance <= maxDistance;
};


