export function calcDistance(lat1, lon1, lat2, lon2) {
  // http://www.movable-type.co.uk/scripts/latlong.html
  var radiusOfTheEarth = 6371;
  var dLat = degreesToRadius(lat2 - lat1);
  var dLon = degreesToRadius(lon2 - lon1);
  var a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(degreesToRadius(lat1)) * Math.cos(degreesToRadius(lat2)) *
    Math.sin(dLon / 2) * Math.sin(dLon / 2);

  var curius = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  var distanceInKm = radiusOfTheEarth * curius;
  return distanceInKm;
}

function degreesToRadius(deg) {
  return deg * (Math.PI / 180)
}

export function currentPosition () {
  return new Promise(
    (resolve, reject) => window.navigator.geolocation.getCurrentPosition(resolve, reject)
  ).then(position => position.coords);
}

export function findPosition (position, googleMaps, geocoder) {
  return new Promise((resolve, reject) => geocoder
    .geocode({ address: `${position},NL` }, (results, status) => {
      if (status === googleMaps.GeocoderStatus.OK) {
        const location = results[0].geometry.location;
        resolve({
          latitude: location.lat(),
          longitude: location.lng(),
        });
      } else if (status === googleMaps.GeocoderStatus.ZERO_RESULTS) {
        resolve(null);
      } else {
        reject();
      }
    })
  );
}
