import template from './adviseur.vue.html';

export default {
  template,
  props: {
    id: String,
    naam: String,
    straat: String,
    huisnummer: String,
    huisnummerext: String,
    plaats: String,
    distance: Number,
    reviews: Number,
    score: [Number, Boolean],
    logoid: String,
    fotoid: String,
  },
  computed: {
    showScore () {
      return parseInt(this.reviews) > 0 && !isNaN(this.score);
    },
    distanceFormatted () {
      return `${this.distance.toFixed(2)} km`;
    },
    adres () {
      return `${this.straat} ${this.huisnummer} ${this.huisnummerext} ${this.plaats}`;
    },
    infoURL () {
      if (this.id) {
        return `https://www.advieskeuze.nl/go/k/${this.id}?c=krpqnb`;
      }
      return `https://www.advieskeuze.nl/go/k/`;
    },
    reviewURL () {
      if (this.id) {
        return `https://www.advieskeuze.nl/go/r/${this.id}?c=krpqnb`;
      }
    },
    imgURL () {
      const id = this.fotoid || this.logoid;
      if (id) {
        return `https://www.advieskeuze.nl/images/getimage/${id}?c=krpqnb`;
      }
    },
    contactURL () {
      if (this.id) {
        return `https://www.advieskeuze.nl/go/a/${this.id}?c=krpqnb`;
      }
    },
  },
};
