<template>
  <form @submit.prevent="submit">
    <div class="o-layout">
      <div class="o-layout__row">
        <div class="o-layout__col-m-4 o-layout__col-s-12">
          <div class="u-margin-bottom--20">
            <h2 :for="_uid+'typeOfAdvice'">Waarvoor?</h2>
            <nn-dropdown
              :options="availableTypesOfAdvices"
              v-model="typeOfAdvice"
              placeholder="Maak een keuze"
              ref="typeOfAdvice"
              errorMessage="Vul in waar u voor zoekt."
              :id="_uid+'typeOfAdvice'"
            ></nn-dropdown>
          </div>

          <nn-radio-button id="input-particulier" v-model="typeOfCustomer" value="particulier">
            Particulier
          </nn-radio-button>

          <nn-radio-button id="input-zakelijk" v-model="typeOfCustomer" value="zakelijk">
            Zakelijk
          </nn-radio-button>

        </div>
        <div class="o-layout__col-m-4 o-layout__col-s-12">
          <h2>Waar?</h2>
          <LocationSelector
            :locationSource="locationSource"
            :onLocationSourceChanged="onLocationSourceChanged"
            :value="location"
            :onValueChanged="onLocationSelectorValueChanged"
            :enableCurrentPositionSelector="!!geocoder"
            :isInvalid="isLocationInvalid"
            name="postcode"
          />
        </div>
        <div class="o-layout__col-m-4 o-layout__col-s-12">
          <div class="u-margin-bottom">
            <h2 :for="_uid+'wie'">Wie?</h2>
            <input
              :id="_uid+'wie'"
              type="text"
              class="c-form-text"
              name="naam"
              placeholder="(Deel van) de naam"
              v-model="wie"
            >
          </div>
        </div>
      </div>
      <div class="o-layout__row">
        <div class="o-layout__col-m-6 o-layout__col-s-12 u-margin-top u-margin-bottom">
          <nn-button type="submit">Zoeken</nn-button>
        </div>
        <div class="o-layout__col-m-6 o-layout__col-s-12 u-margin-top u-margin-bottom">
          <div class="sortyBy">
            <label :for="_uid+'sorteer'">Sorteer op</label>
            <div class="u-margin-left">
              <nn-dropdown
                :id="_uid+'sorteer'"
                v-model="sortBy"
                @change="sort"
                :options="sortByOptions"
                full-width
              ></nn-dropdown>
            </div>
            <div v-if="sortBy === 'score'" class="u-margin-left">
              <nn-dropdown
                :id="_uid+'afstand'"
                v-model="maxDistance"
                @change="onMaxDistanceChanged"
                :options="distanceOptions"
                full-width
              ></nn-dropdown>
            </div>
          </div>
        </div>
        <div class="o-layout__col-m-12">
          <Alert type="warning" v-if="errorMessage">
            {{ errorMessage }}
          </Alert>
        </div>
        <div class="o-layout__col-m-12">
          <nn-spinner v-if="isBusy"></nn-spinner>
          <template v-else-if="resultaten">
            <template v-if="resultaten.length">
              <Adviseur v-for="adviseur in resultaten" v-bind="adviseur" :key="adviseur.vueId" />
            </template>
            <p v-else>
              geen resultaten gevonden
            </p>
          </template>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import Adviseur from './adviseur';
import LocationSelector from './locationSelector';
import { currentPosition, findPosition } from './geocoding';
import {
  findAanstelling,
  queryAdvisors,
  addUniqueId,
  sortAdvisors
} from './utils';

import { error } from '@nn/psdl-utility/src/console';
import { queryString } from '@nn/psdl-utility/src/query-string';

import loadScript from '../utility/loadscript';
import Alert from '../app-vue-shared/alert';

const data =
  process.env.NODE_ENV === 'testing'
    ? require('./adviseurs_testing.json')
    : require('./adviseurs_prod.json');

const sortByOptions = [
  {
    label: 'Afstand',
    value: 'distance'
  },
  {
    label: 'Score',
    value: 'score'
  }
];

const distanceOptions = [
  {
    label: 'tot 3 km',
    value: '3'
  },
  {
    label: 'tot 5 km',
    value: '5'
  },
  {
    label: 'tot 10 km',
    value: '10'
  },
  {
    label: 'tot 15 km',
    value: '15'
  },
  {
    label: 'tot 20 km',
    value: '20'
  },
  {
    label: 'tot 25 km',
    value: '25'
  },
  {
    label: 'tot 50 km',
    value: '50'
  },
  {
    label: 'tot 75 km',
    value: '75'
  },
  {
    label: 'heel NL',
    value: '0'
  }
];

addUniqueId(data.adviseurs);

export default {
  components: { Adviseur, LocationSelector, Alert },
  data: () => ({
    typeOfCustomer: null,
    locationSource: 'manual',
    categoryMap: data.categoryMap,
    typeOfAdvice: null,
    isTypeOfAdviceValidated: false,
    wie: '',
    location: '',
    maxDistance: '10',
    sortBy: 'distance',
    resultaten: undefined,
    isBusy: false,
    geocoder: null,
    valid: null,
    errorMessage: false,
    sortByOptions,
    distanceOptions
  }),
  computed: {
    availableTypesOfAdvices () {
      return this.categoryMap
        .filter(c => c.category.toLowerCase() === this.typeOfCustomer)
        .map(option => {
          return {
            label: option.name,
            value: option.name
          };
        });
    },
    isLocationInvalid () {
      return (
        this.valid === false &&
        this.locationSource === 'manual' && !this.location
      );
    }
  },
  props: {
    apiKey: {
      type: String,
      default: ''
    }
  },
  watch: {
    typeOfCustomer (newVal, oldVal) {
      if (oldVal && newVal !== oldVal) {
        this.typeOfAdvice = null;
      }
    }
  },
  methods: {
    async checkQueryParamsAndSubmit () {
      const aanstelling = findAanstelling(
        this.categoryMap,
        queryString('aanstelling')
      );
      const typeKlant = queryString('typeKlant');
      const location = queryString('location');
      this.typeOfCustomer = typeKlant ? typeKlant.toLowerCase() : 'particulier';
      if (aanstelling && location) {
        this.typeOfAdvice = aanstelling.name;
        if (location === 'current') {
          this.locationSource = 'current';
        } else {
          this.locationSource = 'manual';
          this.location = decodeURIComponent(location);
        }
        await this.submit();
      }
    },
    onLocationSelectorValueChanged (e) {
      this.location = e.target.value;
    },
    onLocationSourceChanged (value) {
      this.locationSource = value;
    },
    onMaxDistanceChanged () {
      this.submit();
    },
    lookupPosition (location) {
      if (this.locationSource === 'manual') {
        return findPosition(location, window.google.maps, this.geocoder);
      } else if (this.locationSource === 'current') {
        return currentPosition();
      }
    },
    sort () {
      if (this.resultaten) {
        this.resultaten = sortAdvisors(this.sortBy, this.resultaten);
      }
    },
    validate () {
      this.$refs.typeOfAdvice.touch();
      this.valid = true;
      if (this.locationSource === 'manual' && !this.location) {
        this.valid = false;
      }
    },
    async submit () {
      this.validate();
      if (!this.valid) {
        return;
      }
      this.isBusy = true;
      this.resultaten = undefined;
      try {
        this.errorMessage = false;
        let position = await this.lookupPosition(this.location);
        if (position) {
          this.resultaten = queryAdvisors(
            [...data.adviseurs],
            this.categoryMap,
            this.typeOfAdvice,
            this.wie,
            parseInt(this.maxDistance),
            position
          );
          this.sort();
        } else {
          this.errorMessage = 'We kunnen uw adres niet vinden.';
        }
      } catch (err) {
        error(err);
        this.errorMessage =
          'Uw adres kan op dit moment niet opgezocht worden. Controleer uw locatie instellingen in uw browser';
      } finally {
        this.isBusy = false;
      }
    }
  },
  async mounted () {
    try {
      if (this.apiKey) {
        await loadScript(
          `https://maps.googleapis.com/maps/api/js?key=${encodeURIComponent(
            this.apiKey
          )}`
        );
        this.geocoder = new window.google.maps.Geocoder();
      }
    } catch (err) {
      error('Google Maps API niet beschikbaar:', err);
    } finally {
      await this.checkQueryParamsAndSubmit();
    }
  }
};
</script>
